.gallery {
  padding: 6px;
  gap: 4px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  align-content: flex-start;
  // height: 100%;
  width: 100%;

  position: relative;
  // top: 0;
  // position: fixed;
  // background-color: map-get($colors, 'gallery');
  overflow: auto;
  // scroll-behavior: smooth;

  & a {
    // background-image: url('https://pixabay.com/get/g3b4c0beb7d52e04c253d5793e41d2a546a9ce322e1527762eae1ba762c7aae115e36a4d785f354629a0ad834987fd98d8e641e7cfc031545124764ce148e4d14_1280.jpg');

    width: 100%;
    display: flex;
    flex-grow: 100;
    overflow: hidden;
    display: block;
  }
  &__filler {
    min-height: 60px;
  }
}

.card {
  // radial-gradient(150px 40px at 50% bottom);
  //  #ff0000 1%, #00ffff00),
  // radial-gradient($gradient),
  // radial-gradient(150px 40px at 195px bottom, #666, #222);
  border-radius: 16px; //5%;
  // border: 1px solid map-get($colors, 'border');
  overflow: hidden;
  padding: 12px;
  gap: 6px;
  background-color: map-get($colors, 'galleryItem');
  // width: map-get($gallery, 'itemWidth');
  // height: map-get($gallery, 'itemHeight');
  flex: 1 0 300px; //1 0 400px; (4 karty)
  aspect-ratio: 4/3; //gpt 3
  // max-height: 55vh;
  max-width: 40vw;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  box-shadow: $box-shadow;

  &__img {
    // background-image: '';
    // display: block;
    // width: map-get($gallery, 'imgWidth');
    // height: map-get($gallery, 'imgHeight');
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    background-image: url(../images/loading.svg);
    background-position: top right, center;
    background-repeat: no-repeat, no-repeat;
    background-size: 80px 80px, cover;
    // background: radial-gradient($gradient);
    border-radius: 16px 16px 16px 16px; //5% 5% 5% 5%;
  }
  &__info {
    // flex: 0 1 auto;
    // flex-wrap: wrap;
    // flex: 1;
    // flex-basis: 200px;
    display: flex;
    align-items: flex-start;
    // justify-content: flex-start;
    justify-content: space-around;
    width: 100%;
    // flex-wrap: wrap;
    // gap: 2px;
    &--entry {
      flex-grow: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;
    }
  }
}
.span--description {
  font-weight: bold;
  margin: 0 4px;
}
.span--number {
  margin: 0 4px;
}
@media screen and (max-width: 900px) {
  .card {
    flex: 1 0 400px; //gpt 2
    padding: 6px;
    // aspect-ratio: 5/2; //gpt 3
    // max-height: 45vh;
    max-width: 100%;
    // max-height: 100vw;
    &__info {
      flex-wrap: wrap;
    }
  }
}
@media screen and (max-width: 900px) and (orientation: landscape) {
  .card {
    // flex: 1 0 400px; //gpt 2
    aspect-ratio: 5/2; //gpt 3
    // max-height: 45vh;
    // max-width: 100%;
    // max-height: 100vw;
  }
}
